import { UnwrapRef } from "vue";

export interface ISTData {
  checked?: boolean;
  disabled?: boolean;
  expand?: boolean;
  showExpand?: boolean;
  [key: string]: any;
}

export interface ISTColumnTagValue {
  color: string;
  text: string;
}

export interface ISTColumnTag {
  [key: number]: ISTColumnTagValue;
  [key: string]: ISTColumnTagValue;
}

export interface ISTColumnFilterMenu {
  text: string;
  value: any;
  checked?: boolean;
}

export interface ISTColumnFilter {
  menus: ISTColumnFilterMenu[] | (() => Promise<ISTColumnFilterMenu[]>);
  confirmText?: string;
  clearText?: string;
  key?: string;
  reName?: (list: ISTColumnFilterMenu[], col: ISTColumn) => Record<string, any>;
  _default?: any;
  _menus?: { value: ISTColumnFilterMenu[] };
  indeterminate?: UnwrapRef<{ value: boolean }>;
  checkAll?: UnwrapRef<{ value: boolean }>;
  _visible?: UnwrapRef<{ value: boolean }>;
  fn?: (filter: ISTColumnFilterMenu, record: ISTData) => boolean; // 
}

export type Sort = "descend" | "ascend";

export interface ISTColumnsSort {
  default?: Sort | undefined;
  compare: ((o1: ISTData, o2: ISTData, order: Sort) => number) | true;
  reName?: (col: ISTColumn, sort: Sort) => Record<string, any>;
}

export interface ISTColumn {
  title?: string;
  renderTitle?: string | ((column: ISTColumn, index: number) => void);
  index?: string | string[] | null | undefined;
  render?: string;
  default?: string;
  width?: string;
  format?: (
    data: ISTData,
    column: ISTColumn,
    index: number,
    realIndex: number
  ) => string;
  iif?: (column: ISTColumn) => boolean;
  noIndex?: number;
  indexKey?: string;
  fixed?: "left" | "right"; // 
  buttons?: ISTColumnButton[];
  type?: "tag" | "link";
  tag?: ISTColumnTag;
  filter?: ISTColumnFilter | null;
  click?: (data: ISTData) => any;
  customRender?: (params: {
    text: string;
    record: ISTData;
    index: number;
  }) => Record<string, any>;
  sorter?: ISTColumnsSort | null;
  ellipsis?: boolean;
  [key: string]: any;
}

export interface ISTReqReNameType {
  pi?: string;
  ps?: string;
}

export interface ISTResReNameType {
  total?: string;
  list?: string;
}

export interface ISTRes {
  reName?: ISTResReNameType; // totallist
  process?: (data: ISTData[], rawData?: any) => ISTData[]; // rawData
}

export interface ISTPage {
  front?: boolean; //  
  position?: "top" | "bottom" | "both";
  show?: boolean;
  showSize?: boolean;
  pageSizes?: number[];
  showQuickJumper?: boolean;
  indexReset?: boolean;
  toTop?: boolean; // 
  toTopOffset?: number; // 
}

export interface ISTReq {
  params?: any; // pipsreq
  headers?: any;
  reName?: ISTReqReNameType; // pips
  firstParams?: any; // 
}

export interface ISTDataSourceOption {
  pi: number;
  ps: number;
  paginator: boolean; // 
  data: string | Promise<ISTData[]> | ISTData[];
  total: number;
  columns: ISTColumn[];
  req: ISTReq;
  res: ISTRes;
  page: ISTPage;
  first: boolean;
}

export interface ISTDataSourceResult {
  /**  */
  pageShow: boolean;
  /**  `pi` `undefined`  */
  pi: number;
  /**  `ps` `undefined`  */
  ps: number;
  /**  `total` `undefined`  */
  total: number;
  list: ISTData[];
}

export interface ISTColumnButton {
  text?: string | ((record: ISTData, btn: ISTColumnButton) => string);
  icon?: string;
  click?: (record: ISTData) => void | "reload";
  children?: ISTColumnButton[]; // 
  iif?: (record: ISTData, column: ISTColumn) => boolean;
  iifBehavior?: "hide" | "disabled"; // iiffalse
  type?: "popconfirm";
  popconfirm?: ISTColumnPopConfirm;
  [key: string]: any;
}

export interface ISTColumnPopConfirm {
  title: string;
  confirm: (record: ISTData) => void;
}

export interface ISTResetColumnsOption {
  pi?: number;
  ps?: number;
  columns?: ISTColumn[];
  preClearData?: boolean;
  emitReload?: boolean;
}

export interface ISTLoadOptions {
  merge: boolean; //
}

export const COLUMN_DEFAULT = "-";
export const NO_INDEX = 1;

export const DEFAULT_ST_RES: ISTRes = {
  reName: { total: "total", list: "items" },
};
export const DEFAULT_ST_REQ: ISTReq = {
  reName: { pi: "offset", ps: "limit" },
};
export const DEFAULT_ST_PAGE: ISTPage = {
  front: true,
  position: "bottom",
  show: true,
  showSize: false,
  pageSizes: [10, 20, 30, 40, 50],
  showQuickJumper: false,
  indexReset: true,
  toTop: true,
  toTopOffset: 100,
};
export const DEFAULT_IIF_BEHAIVOR = "hide";
export const DEFAULT_FILTER = {
  filterConfirmText: "",
  filterClearText: "",
};

export interface IChangeSort {
  column?: ISTColumn;
  columnKey: string;
  order?: Sort | undefined;
}